<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="plan-form-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" :title="formConfig.title" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group class="plan-form-list">
      <div class="plan-item" v-for="item in formConfig.data" :key="item.id">
        <div class="title" v-if="item.title">{{item.title}}</div>
        <div class="plan-item-li" v-for="(content, index) in item.list" :key="index">
          <div class="li-title">{{index + 1}}、{{content.title}}</div>
          <div class="li-content">{{content && content.content}}</div>
          <div class="li-result">
            <van-radio-group v-model="content.result" :disabled="readonly">
              <van-radio name="normal">正常</van-radio>
              <van-radio name="abnormal">异常</van-radio>
              <van-radio name="not">不涉及</van-radio>
            </van-radio-group>
          </div>
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="备注（非必填）"></van-cell>
      <van-field v-model="remark" placeholder="请输入现象" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="故障记录"></van-cell>
      <van-cell @click="handleRecord(item, index)" :title="item.deviceName + ' ' + item.deviceNo" is-link clickable v-for="(item, index) in formConfig.faultList" :key="index"></van-cell>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action" v-if="!readonly">
        <van-button @click="handleAddRecord" class="action-item-main btn-save" color="rgba(16, 142, 233, 0.15)">记录故障</van-button>
        <van-button @click="handleSave" class="action-item-main" color="#108EE9">保存</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import native from '../../utils/native.js'
import {statusEnum, formStatusEnum} from '../../utils/enum.js'

export default {
  components: {
    Page,
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    index: null,
    title: '',
    formConfig: {},
    radio: '',
    remark: '',
    readonly: false
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.id = navigate.getParam('id')

      this.index = navigate.getParam('index') || 0

      this.readonly = navigate.getParam('readonly') || false

      this.loadForm()
    },
    onShow () {
      this.loadForm()
    },
    onClickLeft () {
      navigate.back()
    },

    loadForm () {
      db.getOrderDetail(this.id).then(data => {

        if (data.id) {
          this.formConfig = {
            ...data.formList[this.index]
          }

          console.log(JSON.stringify(this.formConfig))
        } else {
          io.getPlanDetail({id: this.id}).then(data => {
            this.formConfig = {
              ...data.formList[this.index]
            }

            console.log(JSON.stringify(this.formConfig))
          })
        }
      })
    },

    handleAddRecord () {
      navigate.open('plan_record', {id: this.id, index: this.index, editIndex: -1})
    },

    handleRecord (item, index) {
      navigate.open('plan_record', {
        id: this.id, 
        index: this.index, 
        deviceId: item.deviceId, 
        deviceName: item.deviceName, 
        deviceNo: item.deviceName,
        editIndex: index,
        remark: item.remark
      })
    },

    handleSave () {
      db.getOrderDetail(this.id).then(data => {

        let status = 'executed'

        for (let i = 0; i < this.formConfig.data.length; i++) {
          let list = this.formConfig.data[i].list

          for (let j = 0; j < list.length; j++) {
            if (!list[j].result) {
              status = 'doing'
              break
            }
          }

          if (status == 'doing') break
        }

        data.formList[this.index] = {...this.formConfig, status, remark: this.remark}

        db.updateOrder({
          ...data,
        }).then(() => {
          navigate.back()
        })
      })
    },
  }
}
</script>

